const isJSON = str => {
  try {
    JSON.parse(str);
  } catch (e) {
    return false;
  }
  return true;
}

const isValidPenguinConfig = conf => {
  return "@type" in conf && "items" in conf && "options" in conf;
}

const makePenguinRequest = conf => {
  const res = {
    input_lang: "id",
    output_lang: "id",
    server: "US",
    owned: {},
    required: {},
  };
  const options = conf.options;
  const items = conf.items;
  items.forEach(item => {
    if ("have" in item) {
      res.owned[item.id] = item.have;
    }
    if ("need" in item) {
      res.required[item.id] = item.need;
    }
  });
  res.exp_demand = options.requireExp;
  res.extra_outc = options.byProduct;
  res.gold_demand = options.requireLmb;

  return res;
}

export { isJSON, makePenguinRequest, isValidPenguinConfig };