<template>
  <v-form v-model="valid" @submit.prevent="next">
    <v-container>
      <v-row>
        <v-col cols="12">
          <v-text-field
            v-model.number="manual.amount"
            :rules="manualRules"
            label="Repeat"
            type="number"
            required
          >
          </v-text-field>
        </v-col>
      </v-row>
      <v-row class="pt-4">
        <v-btn color="primary" type="submit" :disabled="!valid">
          Continue
        </v-btn>
        <v-btn class="ml-3" text @click="$emit('back')">Back</v-btn>
      </v-row>
    </v-container>
  </v-form>
</template>

<script>
export default {
  name: "OneStage",
  data: () => ({
    valid: true,
    manual: { amount: 1 },
    manualRules: [(v) => v >= 0 || "Invalid amount"],
  }),
  methods: {
    next() {
      this.$store.commit("form/setStages", this.manual);
      this.$emit("next");
    },
  },
};
</script>