<template>
  <v-container class="mt-5">
    <v-row no-gutters justify="center">
      <v-col cols="12" md="8" lg="8">
        <v-card :loading="loading" class="pb-5 pr-5 pl-5">
          <v-card-title>
            <h3>Compose Task</h3>
          </v-card-title>
          <v-divider></v-divider>
          <v-stepper v-model="composeStep" class="mt-3" vertical>
            <v-stepper-step step="1" :complete="composeStep > 1">
              <span>
                Select Farming Mode
                <v-tooltip right>
                  <template v-slot:activator="{ on, attrs }">
                    <v-icon v-bind="attrs" v-on="on" class="pb-1" size="17px">
                      mdi-help-circle-outline
                    </v-icon>
                  </template>
                  <span>
                    <strong>Standard</strong>: Lets you pick the stage(s) and the
                    amount you want to farm <br />
                    <strong>One-Stage</strong>: Once it's started, it will wait for
                    you to enter a stage, then repeats the stage you entered x
                    times <br />
                    <strong>Recent Task</strong>: Continues from your recently
                    halted task (if available)
                  </span>
                </v-tooltip>
              </span>
              <small class="mt-1">
                Determine how you pick the stages to farm
              </small>
            </v-stepper-step>
            <v-stepper-content step="1">
              <v-select
                v-model="selectedMode"
                :composeStep="composeStep"
                :items="mode"
                label="Select Mode"
              >
              </v-select>
              <v-btn
                color="primary"
                :disabled="!selectedMode"
                @click="composeStep = 2"
              >
                Continue
              </v-btn>
            </v-stepper-content>
            <v-stepper-step step="2" :complete="composeStep > 2">
              <span>Pick the stages</span>
            </v-stepper-step>
            <v-stepper-content class="pt-0" step="2">
              <standard
                v-if="selectedMode === 'Standard'"
                @next="composeStep = 3"
                @back="composeStep = 1"
              ></standard>
              <one-stage
                v-else-if="selectedMode === 'One-Stage'"
                @next="composeStep = 3"
                @back="composeStep = 1"
              ></one-stage>
              <standard
                v-else-if="selectedMode === 'Recent Task'"
                :stages="$store.state.recent.stages"
                @next="composeStep = 3"
                @back="composeStep = 1"
              ></standard>
              <penguin v-else @next="composeStep = 3" @back="composeStep = 1">
              </penguin>
            </v-stepper-content>
            <v-stepper-step step="3" :complete="composeStep > 3">
              <span>Refill</span>
              <small class="mt-1">
                How many sanity potions / Originite Prime you want to use
              </small>
            </v-stepper-step>
            <v-stepper-content class="pt-0" step="3">
              <refill
                :selectedMode="selectedMode"
                @next="start"
                @back="composeStep = 2"
              ></refill>
            </v-stepper-content>
          </v-stepper>
        </v-card>
      </v-col>
    </v-row>
  </v-container>
</template>

<script>
import Standard from "../components/forms/Standard.vue";
import Refill from "../components/forms/Refill.vue";
import OneStage from "../components/forms/OneStage.vue";
import Penguin from "../components/forms/Penguin.vue";
import { start, startOneStage } from "../services/farmer";
export default {
  mounted() {
    const recent = this.$store.state.recent.stages;
    if (recent.length > 0) {
      this.mode.push("Recent Task");
    }
  },
  name: "Create",
  props: {
    ws: WebSocket,
  },
  components: {
    Standard,
    Refill,
    OneStage,
    Penguin,
  },
  data: () => ({
    composeStep: 1,
    mode: ["Standard", "One-Stage"],
    selectedMode: null,
    loading: false,
  }),
  methods: {
    start() {
      this.loading = true;
      this.composeStep += 1;
      const { stages, refill } = this.$store.state.form;
      if (Array.isArray(stages)) {
        start(stages, refill).catch((err) => console.log(err.response));
      } else {
        startOneStage(stages.amount, refill).catch((err) =>
          console.log(err.response)
        );
      }
    },
  },
};
</script>
