<template>
  <v-form v-model="valid" @submit.prevent="next">
    <v-container>
      <v-row>
        <v-col cols="12">
          <v-textarea
            v-model="config"
            :rules="configRules"
            label="Configuration code"
            placeholder='{"@type":"@penguin-statistics/planner/config","items":[{"id":"30083","need":2},{"id":"30014","need":7},{"id":"30013","need":8},{"id":"30033","need":2}],"options":{"byProduct":false,"requireExp":false,"requireLmb":false},"excludes":[]}'
            style="font-family: monospace; font-size: 0.7rem"
            outlined
          >
          </v-textarea>
        </v-col>
      </v-row>
      <v-row class="pt-1">
        <v-btn
          :loading="loading"
          color="primary"
          type="submit"
          :disabled="!valid"
        >
          Continue
        </v-btn>
        <v-btn class="ml-2" text @click="$emit('back')">Back</v-btn>
      </v-row>
    </v-container>
  </v-form>
</template>

<script>
import { isJSON, isValidPenguinConfig, makePenguinRequest } from "../../utils";
import { plan } from "../../services/penguin";
export default {
  name: "Penguin",
  data: () => ({
    valid: true,
    loading: false,
    config: "",
    configRules: [
      (v) =>
        (isJSON(v) && isValidPenguinConfig(JSON.parse(v))) ||
        "Configuration code is not valid",
      (v) => !!v || "Configuration code must not be empty",
    ],
  }),
  methods: {
    next() {
      this.loading = true;
      const penguinRequest = makePenguinRequest(JSON.parse(this.config));
      plan(penguinRequest)
        .then((res) => {
          console.log(res);
        })
        .finally(() => {
          this.loading = false;
        });
      this.$store.commit("form/setRefill", this.refill);
      this.$emit("next");
    },
  },
};
</script>