<template>
  <v-form v-model="valid" @submit.prevent="next">
    <v-container>
      <v-row v-for="(input, i) in stages" :key="i">
        <v-col cols="9">
          <v-text-field
            v-model="input.stage"
            :rules="stageRules"
            label="Stage"
            placeholder="1-7"
            required
          >
          </v-text-field>
        </v-col>
        <v-col cols="2">
          <v-text-field
            v-model.number="input.count"
            :rules="amountRules"
            label="Amount"
            type="number"
            required
          >
          </v-text-field>
        </v-col>
        <v-col cols="1">
          <v-btn icon class="mt-4" @click="removeStage(i)">
            <v-icon>mdi-close</v-icon>
          </v-btn>
        </v-col>
      </v-row>
      <v-btn class="mt-2" outlined block @click="addStage">
        <v-icon left> mdi-plus-circle-outline </v-icon>
        Add Stage
      </v-btn>
      <v-row class="pt-4">
        <v-col>
          <v-btn
            color="primary"
            type="submit"
            :disabled="!valid || stages.length === 0"
          >
            Continue
          </v-btn>
          <v-btn class="ml-2" text @click="$emit('back')"> Back </v-btn>
        </v-col>
      </v-row>
    </v-container>
  </v-form>
</template>

<script>
export default {
  name: "Standard",
  props: {
    stages: {
      type: Array,
      default: () => [
        {
          stage: "",
          count: 1,
        },
      ],
    },
  },
  data: () => ({
    valid: false,
    stageRules: [
      (v) => !!v || "Stage must not be empty",
      (v) =>
        /^\d{1}-\d{1,2}$/.test(v) || // Normal stages
        /^\w{2}-\d{1}$/.test(v) || // Supply / Event stages
        /^\w{2}-\w{1}-\d{1}/.test(v) || // Chip stages
        "Invalid stage",
    ],
    amountRules: [(v) => v > 0 || "Invalid amount"],
  }),
  methods: {
    addStage() {
      this.stages.push({
        stage: "",
        count: 1,
      });
    },
    removeStage(key) {
      this.$delete(this.stages, key);
    },
    next() {
      this.$store.commit("form/setStages", this.stages);
      this.$emit("next");
    },
  },
};
</script>