<template>
  <v-form v-model="valid" @submit.prevent="next">
    <v-container>
      <v-row>
        <v-col cols="12">
          <v-text-field
            v-model.number="refill"
            :rules="refillRules"
            label="Refill"
            type="number"
            required
          >
          </v-text-field>
        </v-col>
      </v-row>
      <v-row class="pt-1">
        <v-col cols="12" class="pt-0">
          <v-alert
            v-if="selectedMode !== 'One-Stage'"
            type="warning"
            border="left"
            dense
            text
            outlined
          >
            Please set yourself in <strong>main menu</strong> screen before
            starting.
          </v-alert>
        </v-col>
      </v-row>
      <v-row class="pt-3">
        <v-btn color="primary" type="submit" :disabled="!valid"> Start </v-btn>
        <v-btn class="ml-2" text @click="$emit('back')">Back</v-btn>
      </v-row>
    </v-container>
  </v-form>
</template>

<script>
export default {
  name: "Refill",
  props: {
    selectedMode: String,
  },
  data: () => ({
    valid: true,
    refill: 0,
    refillRules: [(v) => v >= 0 || "Invalid amount"],
  }),
  methods: {
    next() {
      this.$store.commit("form/setRefill", this.refill);
      this.$emit("next");
    },
  },
};
</script>
